import {fetchGet} from '@/https'

// 根据id获取用户信息
export const getUserInfoById = () => {
  return fetchGet(`/petsCategory/list`) // resfulapi风格
}

// 品种列表
export const requestSpeciesList = (params) => {
  return fetchGet('/petsSpecies/list', params)
}

// 品种详情
export const requestSpeciesDetail = (id) => {
  return fetchGet('/petsSpecies/detail/' + id)
}
